/* Center the whole component vertically and horizontally */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Center vertically within the viewport */
  width: 100%; /* Ensure the container takes full width */
  max-width: 600px; /* Maximum width */
  margin: auto;
  font-family: Arial, sans-serif;
  padding: 20px;
  background-color: #ccd1cc;
  border-radius: 15px; /* Rounded corners */
}

/* Input field styles */
.input {
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  width: 80%;
  max-width: 400px;
}

/* Button styles */
.button {
  padding: 10px 20px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 20px;
}

/* QR code container styling */
.qr-code-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  width: 80%;
  max-width: 500px;
  margin-top: 20px;
}

/* Heading styles */
.heading {
  margin-bottom: 20px;
  color: #333;
}
